/*****Queries container*****/
.container {
	display: grid;
	grid-template-columns: 1fr;
    margin-right: auto;
    margin-left: auto;
    padding-right: 15px;
    padding-left: 15px;
	text-align: center;
}

@media (min-width: 370px) {
    .container {
      max-width: 360px;
    }
}

@media (min-width: 576px) {
    .container {
      max-width: 540px;
    }
}
  
@media (min-width: 768px) {
    .container {
      max-width: 720px;
    }
}
  
@media (min-width: 992px) {
    .container {
      max-width: 960px;
    }
}
  
@media (min-width: 1200px) {
    .container {
      max-width: 1140px;
    }
}

