/********
HEADER
*********/
header {
    text-align: center;
}
/* Menú de navegación*/
nav ul li {
    text-transform: uppercase;
    font-size: 1.1rem;
    font-weight: 400;
	padding-left: 0;
	list-style: none;
}

nav ul li a {
